import React, { Component, Fragment } from 'react';
// НАДА УДАЛИТЬ!!!
// import Calendar from 'react-calendar-material';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';

import Calendar from 'react-big-calendar'
import moment from 'moment'
import withDragAndDrop from "react-big-calendar/lib/addons/dragAndDrop";


import "react-big-calendar/lib/addons/dragAndDrop/styles.css";
import "react-big-calendar/lib/css/react-big-calendar.css";

import AddEventModal from './components/AddEventModal'


const GET_EVENTS_OF_GROUP = gql`
    query group($id: String!) {
        group(id: $id) {
            id_group
            title_group
            institute_id
            events {
                id_event
                title_event
                description
                start_time
                end_time
                id_group
            }
        }
    }
`;
Calendar.setLocalizer(Calendar.momentLocalizer(moment));

const DnDCalendar = withDragAndDrop(Calendar);

class Admin extends Component {

    state = {
        events: false,
        add_modal_visible: false
      };

    componentDidMount() {
        // console.log(new Date(moment().add(120, "minutes")))
        // function eventFire(el, etype){
        //     if (el.fireEvent) {
        //       el.fireEvent('on' + etype);
        //     } else {
        //       var evObj = document.createEvent('Events');
        //       evObj.initEvent(etype, true, false);
        //       el.dispatchEvent(evObj);
        //     }
        
        // }
        // eventFire(document.getElementById('mytest1'), 'click');
    }
    onEventResize = (type, { event, start, end, allDay }, data) => {
        this.setState({
            events: data
        }, () => {
            this.setState(state => {
                state.events[0].start = start;
                state.events[0].end = end;
                return { events: state.events };
              });
        })

      };

    
    onEventDrop = ({ event, start, end, allDay },data) => {
        console.log(start);
        this.setState({
            events: data
        }, () => {
            this.setState(state => {
                state.events[0].start = start;
                state.events[0].end = end;
                return { events: state.events };
              });
        })

    };

    returnData = (new_data) => {
        if (this.state.events) {
            return this.state.events
        } else {
            return new_data
        }
    }

    onSelect = (e,data) => {
        this.setState({
            events: data
        }, () => {
            this.setState({
                add_modal_visible: true
            })
        })
    }

    filterData = (data) => {
        let new_data = []
        data.group.events.forEach(element => {
            console.log(                        
            element.start_time.substr(0,4),
            element.start_time.substr(4,2),
            element.start_time.substr(6,2),
            element.start_time.substr(8,2),
            element.start_time.substr(10,2),
            element.start_time.substr(12,2))
                new_data.push({
                    id: element.id_event,
                    title: element.title_event,
                    description: element.description,
                    start: new Date(
                        element.start_time.substr(0,4),
                        element.start_time.substr(4,2)-1,
                        element.start_time.substr(6,2)-1,
                        element.start_time.substr(9,2),
                        element.start_time.substr(11,2),
                        element.start_time.substr(13,2)
                    ),
                    end: new Date(
                        element.end_time.substr(0,4),
                        element.end_time.substr(4,2)-1,
                        element.end_time.substr(6,2)-1,
                        element.end_time.substr(9,2),
                        element.end_time.substr(11,2),
                        element.end_time.substr(13,2)
                    ),
                })
            });
        return <DnDCalendar
                defaultDate={new Date()}
                defaultView="week"
                // onSelecting = {(e) => this.onSelect(e)}
                // onSelectSlot = {(e) => this.onSelect(e, new_data)}
                selectable = {true}
                events={this.returnData(new_data)}
                onEventDrop={arg => this.onEventDrop(arg, new_data)}
                onEventResize={(type, arg) => this.onEventResize(type, arg, new_data)}
                resizable
                style={{ height: "100vh"}}
            />
    }


    render () {
        return <Fragment>
            {this.state.add_modal_visible && (
            <div 
            style={{
                position: "fixed", 
                width: "100vw", 
                height: "100vh",
                top: 0, 
                left:0, 
                backgroundColor: "rgba(0,0,0,0.2)",
                zIndex: 100,
                display: "flex",
                justifyContent: 'center',
                alignItems: 'center'
            }}
            >
             <div style={{position: "absolute",top: 0,left: 0,width: "100%", height: "100%",zIndex:1}} onClick = {()=>this.setState({add_modal_visible: false})}></div>
             <AddEventModal data={this.state.add_event_data}></AddEventModal>
            </div>
            )}
            <div style={{maxWidth:800}}>
            <Query 
                query={GET_EVENTS_OF_GROUP}
                variables={{id:"43525"}}
            >
                {({ data, loading }) => {
                    if (loading) {
                        return <div>Loading ...</div>;
                    }
                    return this.filterData(data);
                }}
            </Query>
            </div>
            </Fragment>
    }
}
export default Admin